import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TableFooter from '@material-ui/core/TableFooter';

const useStyles = makeStyles({
  table: {
    minWidth: 480,
  },
});

function createData(name: string, damagePercent: number, damage: number, dps: number) {
  return { name, damage, damagePercent, dps };
}

export default function DamageTable(props: { data: { name: string, totalDamage: number, damage: Array<number[]> }[][], totalTime: number }) {

  const classes = useStyles();

  const [data, setData] = useState<{ name: string, damage: number, damagePercent: number, dps: number }[]>([])
  const [totalDPS, setTotalDPS] = useState<number>(0)

  useEffect(() => {
    let newData: { name: string, damage: number, damagePercent: number, dps: number }[] = []
    let found: { name: string, damage: number, damagePercent: number, dps: number }
    let totalDamage: number = 0
    let totalDPS: number = 0

    props.data.forEach((val) => {
      val.forEach((inst) => {
        totalDamage += inst.totalDamage
        found = newData.find((f) => f.name === inst.name)
        if (found) {
          found.damage = found.damage + inst.totalDamage

          found = { name: inst.name, damage: found.damage + inst.totalDamage, damagePercent: 0, dps: 0 }
        } else {
          newData.push({ name: inst.name, damage: inst.totalDamage, damagePercent: 0, dps: 0 })
          found = newData.find((f) => f.name === inst.name)
        }
      })
    })

    newData.forEach((val) => {
      val.damagePercent = val.damage / totalDamage
      val.dps = val.damage / props.totalTime
      totalDPS = totalDPS + val.dps
      val.damage = val.damage / props.data.length
    })

    console.log("recalc table")

    setData(newData.sort((a, b) => b.damage - a.damage))
    setTotalDPS(totalDPS)
  }, [props.data])

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} size='small' aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="left">Name</TableCell>
            <TableCell align="right">Damage</TableCell>
            <TableCell align="right">Damage(%)</TableCell>
            <TableCell align="right">DPS</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row) => (
            <TableRow key={row.name}>
              <TableCell>{row.name}</TableCell>
              <TableCell align="right">{row.damage.toFixed(2)}</TableCell>
              <TableCell align="right">{(row.damagePercent * 100).toFixed(2)}%</TableCell>
              <TableCell align="right">{(row.dps).toFixed(2)}</TableCell>
            </TableRow>
          ))}
        </TableBody>
        <TableFooter>
          <TableRow>
            <TableCell size='medium' align="left">Total</TableCell>
            <TableCell variant='body' size='medium' colSpan={3} align="right">{totalDPS.toFixed(2)}</TableCell>
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  );
}
